import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
// Plugins Vuex
import VuejsStorage from 'vuejs-storage';
// Default language by vuetify
import Vuetify from '../src/plugins/vuetify';
// api example
import exampleJson from './ApiExample';

Vue.use(Vuex);

// Configuración básicas axios
const AXIOS_DEFAULT_CONFIG = {
    baseURL: 'https://reqres.in/api',
    timeout: 15000, // Si nunca responde el servidor
    responseType: 'json',
};

export default new Vuex.Store({
  state: {
    user: {},
    config: null,
    selectedLanguage: Vuetify.preset.lang.current,
    languages: [
      // Iconos tomados de: https://www.flaticon.com/packs/countrys-flags
      {title: 'Español', i18n: 'es', icon: 'https://image.flaticon.com/icons/png/512/197/197593.png'},
      {title: 'Ingles', i18n: 'en', icon: 'https://image.flaticon.com/icons/png/128/197/197484.png'},
    ],
    schools: exampleJson.data,
    organizer: {
      selectedSchool: exampleJson.data[0],
      selectedPeriod: new Date().getFullYear(),
      selectedUnit: null,
      selectedOa: null,
      selectedIE: null,
    },
    info: {
      contact: {
        phone: '+569 3458 0660',
        sac: '229293270',
        email: 'sac@romacl.com',
        location: {lat: '10.2099343', lon: '-68.0119684,12z'},
      },
      social: [
        {icon: 'instagram', url: 'http://instagram.com/'},
        {icon: 'facebook', url: 'http://facebook.com/'},
        {icon: 'youtube', url: 'http://youtube.com/'},
        // {icon: 'twitter', url: 'http://twitter.com/'},
      ],
    },
  },
  getters: {
    getSchoolById(state) {
      return (id: number) => {
        return state.schools.find((school: object) => school.id === id);
      };
    },
    // Filter Units
    getUnitById(state, getters) {
      return (schoolId: number, unitId: number) => {
        schoolId = schoolId || state.organizer.selectedSchool.id;
        unitId = unitId || state.organizer.selectedUnit.id;
        return getters.getSchoolById(schoolId)
                      .units
                      .find(
                          (unit: object) => parseInt(unit.id, 16) === parseInt(unitId, 16));
      };
    },
    getUnitsByPlan (state, getters) {

    },
    getUnitsByName (state, getters) {

    },
    getUnitsBySubject (state, getters) {

    },
    getUnitsByCourse (state, getters) {

    },
    getUnitsByLevel (state, getters) {

    },
  },
  mutations: {
    login(state, user) {
      state.user = user;
    },
    selectLanguage(state, language) {
      Vuetify.framework.lang.current = language; // Cambia lenguaje de vuetify
      state.selectedLanguage = language;
    },
    selectSchool(state, school) {
      state.organizer.selectedSchool = school;
    },
    selectPeriod(state, period) {
      state.organizer.selectedPeriod = period;
    },
    selectUnit(state, unit) {
      state.organizer.selectedUnit = unit;
    },

    // Unit Mutations
    addNewUnit(state, unit) {
      if (state.organizer.selectedSchool) {
        state.organizer.selectedSchool.units.push(unit);
      }
    },
    delUnit(state, unitId) {
      if (state.organizer.selectedSchool) {
        state.organizer.selectedSchool.units.splice(unitId, 1);
        // Vue.delete(school.units, unitId);
      }
    },
  },
  actions: {
    async login({ commit }, request) {
        try {
            const ran = Math.floor(Math.random() * (10 - 1)) + 1;
            const user = await axios({
              ...AXIOS_DEFAULT_CONFIG,
              // propiedades no comunes
              url: '/users/' + ran,
              method: 'get',
              data: request,
            });

            commit('login', user.data.data);
        } catch (errors) {
          console.log(errors);
        }
    },
  },
  plugins: [
    VuejsStorage({
      keys: ['selectedLanguage', 'organizer'],
      namespace: 'roma',
      driver: VuejsStorage.drivers.localStorage,
    }),
  ],
});
