



































































































































import Vue from 'vue';
import { mapState, mapMutations } from 'vuex';
import menuJSON from '../../menu.json';

export default Vue.extend({
    name: 'RomaHeader',
    props: {
        navigationMenu: {
          type: Boolean,
          default: true,
        },
    },
    data() {
        return {
            selectedSchool: this.$store.state.organizer.selectedSchool,
            yearSelected: new Date().getFullYear().toString(),
            openedMenu: false,
            navigation: menuJSON
        };
    },
    computed: {
        menu() {
            return [
                { title: this.$vuetify.lang.t('$vuetify.generals.profile'), route: {}},
                { title: this.$vuetify.lang.t('$vuetify.generals.changePassword'), route: {}},
                { title: this.$vuetify.lang.t('$vuetify.generals.help'), route: {}},
                { title: this.$vuetify.lang.t('$vuetify.generals.closeSession'), route: {}},
            ];
        },
        school: {
            get () {
                return this.$store.state.organizer.selectedSchool;
            },
            set (school) {
                this.selectedSchool = school
            }
        },
        ...mapState([
            'schools',
        ]),
    },
    methods: {
      ...mapMutations({
          selectSchool(commit) {
             commit('selectSchool', this.selectedSchool);
         },
      }),
    },
});
